.carousel {
  overflow: hidden;
  position: relative;
}

.carousel:hover #carousel-prev {
  left: 0;
}

.carousel:hover #carousel-next {
  right: 0;
}

.carousel_arrow {
  cursor: pointer;
  color: #707070;
  font-size: 3.5rem;
  position: absolute;
  transition: all 0.3s ease;
  top: 50%;
  transform: translateY(-50%);
  z-index: 50;
}

#carousel-prev {
  left: -1.5rem;
}

#carousel-next {
  right: -1.5rem;
}

.carousel_overlay {
  height: 100%;
  overflow: hidden;
  width: 100%;
}

.carousel_inner {
  display: flex;
  transition: margin-left 0.5s ease-in;
}

.carousel_radio {
  display: none;
}

.radio1:checked ~ .carousel_overlay .carousel_inner {
  margin-left: 0%;
}

.radio2:checked ~ .carousel_overlay .carousel_inner {
  margin-left: -100%;
}

.radio3:checked ~ .carousel_overlay .carousel_inner {
  margin-left: -200%;
}

.radio4:checked ~ .carousel_overlay .carousel_inner {
  margin-left: -300%;
}

.roboto_font {
  font-family: "Roboto", arial, sans-serif;
}

.hover_slide {
  position: relative;
}

.hover_text {
  position: relative;
  transition: color 0.3s ease;
  z-index: 10;
}

.hover_color {
  left: 0;
  height: 100%;
  position: absolute;
  top: 0;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.3s ease, background-color 0.3s ease;
  width: 100%;
}

.hover-slide-up {
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out !important;
}

.hover-slide-up:hover {
  transform: translateY(-0.5rem);
}

.quiver-tag {
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
}

.bottomnav .dark-link > a {
  color: #707070;
}

.bottomnav .dark-link:hover > a {
  color: #323432;
}

.dark-hover {
  color: #323432;
}

.dark-hover:hover {
  color: white !important;
}

.dark-hover:hover + .hover_color {
  background-color: #323432 !important;
}

.nav-sticky .dark-link > a {
  color: #b2afaf;
}

.nav-sticky .dark-link:hover > a {
  color: #f1f5f1;
}

.scale-trigger {
  animation-duration: 1s;
  transition: opacity 0.5s ease;
}

.scale-trigger-on {
  animation-name: scale-up;
}

.translate-left,
.translate-right,
.translate-up {
  opacity: 0;
  transition: transform 0.5s ease-in, opacity 0.5s ease-in;
}

.translate-left {
  transform: translateX(2rem);
}

.translate-right {
  transform: translateX(-2rem);
}

.translate-up {
  transform: translateY(2rem);
}

.translate-slide {
  opacity: 1;
  transform: translate(0);
}

@keyframes scale-up {
  0% {
    opacity: 0;
    transform: translateX(-50%) scale(1);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.3);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}
