  transition: transform 0.2s ease-in-out;
}
.footer__social_icon:hover {
  transform: scale(1.1);
}
.footer__main {
  display: grid;
  grid-template-columns: minmax(20rem, 25rem) repeat(3, 1fr);
  grid-template-rows: 22rem auto;
  padding: 4rem 8rem;
}
.footer__link_block {
  grid-row: 1/2;
}
.footer__link_block:not(:nth-of-type(1)) {
  padding: 2.875rem 0 2.875rem 1rem;
}
.footer__link_block:nth-of-type(1) {
  border-right: 1px solid rgba(0, 0, 0, 0.5);
  grid-column: 1/2;
}
.footer__link_block:nth-of-type(2) {
  grid-column: 2/3;
}
.footer__link_block:nth-of-type(3) {
  grid-column: 3/4;
}
.footer__link_block:nth-of-type(4) {
  grid-column: 4/5;
}
.footer__quiver_info {
  color: #323432;
  padding: 2rem 3rem 2rem 0;
}
.footer__title {
  font-size: 1.125rem;
}
.footer__link {
  color: #323432;
  display: block;
  margin: 1rem 0;
}
.footer__copyright {
  font-size: 0.875rem;
  grid-column: 1/2;
  grid-row: 2/3;
  padding-top: 5rem;
}

@media (max-width: 1000px) {
  .topnav {
    display: none !important;
  }

  .mbl_nav {
    display: flex;
  }

  .bottomnav_container {
    display: block;
  }

  .quiver-tag {
    display: none;
  }

  .bottomnav {
    background-color: white;
    bottom: 0;
    height: auto !important;
    overflow-y: scroll;
    padding: 0 1rem;
    position: absolute;
    right: -18.75rem;
    top: 0;
    transition: right 0.5s ease-in-out;
    width: 18.75rem;
  }
  .bottomnav__container {
    border: none;
  }
  .bottomnav__container, .bottomnav__close, .bottomnav__block, .bottomnav__links {
    display: block;
  }
  .bottomnav__brand {
    display: none;
  }
  .bottomnav__links, .bottomnav__link, .bottomnav__drop_icon {
    margin: 0 !important;
  }
  .bottomnav__link {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1.2rem 0.7rem 1.2rem 0;
  }
  .bottomnav__link > a {
    color: #566658 !important;
    font-size: 1.1rem;
  }
  .bottomnav__link:hover > a {
    color: #4b4b4b !important;
  }
  .bottomnav__contact {
    opacity: 1;
    pointer-events: auto;
    color: #566658;
    display: block;
    font-size: 1.1rem;
    padding: 0;
    margin: 1.2rem 0;
  }
  .bottomnav__media {
    display: flex;
  }

  .dropdown_container {
    opacity: 1;
    pointer-events: auto;
    display: none;
    padding: 0;
    position: static;
    width: 100%;
  }

  .dropdown {
    padding: 0;
  }
  .dropdown__ul {
    background-color: white;
  }
  .dropdown__item {
    margin-top: 0.5rem !important;
    position: relative;
  }
  .dropdown__item:hover {
    background-color: white;
  }
  .dropdown__item:hover .dropdown__link::after {
    opacity: 0;
  }
  .dropdown__drop_icon {
    cursor: pointer;
    color: #707070;
    display: block;
    font-size: 1.2rem;
    position: absolute;
    right: 0;
    top: 0.5rem;
  }
  .dropdown__drop_icon i {
    float: right;
    pointer-events: none;
  }
  .dropdown__link {
    font-size: 1rem;
    padding: 0.5rem;
  }
  .dropdown__mini_text {
    display: none;
  }
  .dropdown__sub {
    opacity: 1;
    pointer-events: auto;
    display: none;
    padding: 0;
    position: static;
  }
  .dropdown__sub_container {
    background-color: white;
    padding: 0;
  }
  .dropdown__sub_links {
    font-size: 0.875rem;
    margin: 0 0 1rem 1rem;
  }

  .landing_hero {
    font-size: 3rem;
  }

  .landing_text br {
    display: none;
  }

  .flex-100 {
    flex-wrap: wrap;
  }

  .footer__block {
    flex-wrap: wrap;
  }
  .footer__container {
    width: 100%;
  }
  .footer__main {
    grid-template-columns: clamp(20rem, 42vw, 25rem) repeat(3, 1fr);
  }
}
@media (max-width: 800px) {
  .landing_hero {
    font-size: 2rem !important;
    line-height: 2.5rem !important;
