    font-size: 1rem !important;
    line-height: 2rem !important;
  }

  .center-container {
    padding: 0 2rem;
  }

  .footer__block {
    padding: 1rem 2rem;
  }
  .footer__container {
    width: 100%;
  }
  .footer__contact {
    margin-right: 0;
  }
  .footer .contact_icon {
    margin-right: 0;
  }
  .footer__contact::after {
    display: none;
  }
  .footer__text {
    font-size: 0.875rem;
  }
  .footer__main {
    display: block;
    padding: 2rem;
  }
  .footer__link_block:nth-of-type(1) {
    border-right: none;
    margin-bottom: 2rem;
